@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

.disable-hover { pointer-events: none !important; }

* { box-sizing: border-box; }

a { color: inherit; }

/* Slider */
.slick-slider { position: relative; display: block; -moz-box-sizing: border-box; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

.icon { fill: currentColor; }

.icon-arrow-left { font-size: 14px; width: 0.8em; height: 1em; }

.icon-arrow-right { font-size: 13px; width: 0.7em; height: 1em; }

.icon-fb { font-size: 430.11px; width: 0.5em; height: 1em; }

.icon-insta-original { font-size: 169.06px; width: 1em; height: 1em; }

.icon-logo { font-size: 53px; width: 2.9em; height: 1em; }

.icon-rect-bot { font-size: 3px; width: 4.4em; height: 1em; }

.icon-rect { font-size: 13px; width: 0.3em; height: 1em; }

.icon-tw { font-size: 497.12px; width: 1.3em; height: 1em; }

.icon-vintage { font-size: 12.94px; width: 4.7em; height: 1em; }

.icon-yt { font-size: 63px; width: 1.5em; height: 1em; }

@font-face { font-family: "PTSans-Regular"; src: url("../fonts/PTSans-Regular.eot"); src: url("../fonts/PTSans-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/PTSans-Regular.woff") format("woff"), url("../fonts/PTSans-Regular.ttf") format("truetype"); font-weight: normal; font-style: normal; }

@font-face { font-family: "OpenSans-Extrabold"; src: url("../fonts/OpenSans-Extrabold.eot"); src: url("../fonts/OpenSans-Extrabold.eot?#iefix") format("embedded-opentype"), url("../fonts/OpenSans-Extrabold.woff") format("woff"), url("../fonts/OpenSans-Extrabold.ttf") format("truetype"); font-weight: normal; font-style: normal; }

.btn { display: inline-block; width: 300px; height: 60px; line-height: 60px; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; color: #ffffff; text-align: center; text-transform: uppercase; transition: all 0.5s ease-out; }

.btn .btn__icon { display: inline-block; vertical-align: middle; height: 14px; width: 3px; position: relative; left: 15px; top: 0; transition: all 0.5s ease-out; transform: translate3d(0, 0, 0); }

.btn .btn__icon:before, .btn .btn__icon:after { content: ''; position: absolute; display: block; right: 0; width: 3px; height: 8px; background-color: #ffffff; transition: all 0.5s ease-out; transform: translate3d(0, 0, 0); }

.btn .btn__icon:before { margin-top: 4px; bottom: 50%; }

.btn .btn__icon:after { margin-top: -4px; top: 50%; }

.btn .btn__icon_red:before, .btn .btn__icon_red:after { background-color: #a70a3f; }

.btn:hover .btn__icon:before { transform: rotate(-45deg) translateZ(0); }

.btn:hover .btn__icon:after { transform: rotate(45deg) translateZ(0); }

.btn_red { background-color: #a70a3f; }

.btn_black { background-color: #000000; }

.btn_white { color: #a70a3f; background-color: #ffffff; }

.arrow-bot-anim { display: inline-block; vertical-align: middle; height: 14px; width: 3px; position: relative; transform: rotate(90deg) translateZ(0); transition: all 0.5s ease-out; }

.arrow-bot-anim:before, .arrow-bot-anim:after { content: ''; position: absolute; display: block; right: 0; width: 3px; height: 8px; background-color: #000; transition: all 0.5s ease-out; transform: translate3d(0, 0, 0); }

.arrow-bot-anim:before { margin-top: 4px; bottom: 50%; }

.arrow-bot-anim:after { margin-top: -4px; top: 50%; }

.arrow-bot-anim.is-active:before { transform: rotate(-45deg) translateZ(0); }

.arrow-bot-anim.is-active:after { transform: rotate(45deg) translateZ(0); }

/* =================================== Main Styles @mixin ================================== */
.main { position: relative; z-index: 1; padding-top: 38px; }

.line-vertical { pointer-events: none; position: absolute; z-index: 1; top: 0; width: 1px; bottom: 0; }

.line-vertical:before, .line-vertical:after { content: ''; position: absolute; z-index: 1; top: 0; left: 0; display: block; width: 100%; height: 100%; background-color: #f5f5f5; }

.line-vertical_left { margin-left: -601px; left: 50%; }

.line-vertical_left:after { transform: translateX(300px); z-index: 2; }

.line-vertical_right { margin-right: -601px; right: 50%; }

.line-vertical_right:after { transform: translateX(-300px); }

.line-vertical_mid { left: 50%; }

.line-vertical_mid:after { display: none; content: none; }

body { position: relative; min-width: 320px; font-family: "PTSans-Regular", Arial, sans-serif; font-size: 14px; color: #000; line-height: 1.2; -webkit-font-smoothing: antialiased; background-color: #ffffff; }

.wrapper { position: relative; overflow: hidden; margin: 0 auto; min-height: 100%; }

.main { position: relative; }

.container { position: relative; z-index: 2; margin-left: auto; margin-right: auto; padding: 0 40px; max-width: 1280px; }

.preloader { display: none; position: fixed; top: 0; left: 0; right: 0; bottom: 0; z-index: 1000; background-color: #ffffff; transition: all 0.75s ease-in-out; }

.is-loaded .preloader { opacity: 0; pointer-events: none; }

/* Helpers ----------------------------------- */
button { background-color: transparent; }

a:hover, a:focus { text-decoration: none; }

a { color: inherit; text-decoration: none; }

table { vertical-align: top; }

img { max-width: 100%; vertical-align: top; }

.icon { display: inline-block; vertical-align: middle; }

.text-left { text-align: left; }

.text-center { text-align: center; }

.text-right { text-align: right; }

.is-hidden { display: none; }

.vertical { display: inline-block; vertical-align: middle; }

.cfx:after { content: ""; display: table; clear: both; }

* ::selection { color: #fff; background-color: #a70a3f; }

* ::-moz-selection { color: #fff; background-color: #a70a3f; }

/* =================================== Header @mixin ================================== */
.header { position: absolute; top: 0; left: 0; right: 0; z-index: 100; background-color: #f5558b; opacity: 0; transform: translateY(-40px); transition: all 1s ease-in-out; }

.is-loaded .header { opacity: 1; transform: translateY(0); }

.header__top { position: relative; z-index: 2; font-family: "OpenSans-Extrabold", Arial, sans-serif; color: #ffffff; line-height: 38px; background-color: #000000; opacity: 0; transform: translateY(-40px); transition: all 1s ease-in-out; }

.header__top:after { content: ""; display: table; clear: both; }

.is-loaded .header__top { opacity: 1; transform: translateY(0); }

.header__phone { float: left; position: relative; padding-left: 57px; }

.header__phone:before { content: ''; position: absolute; z-index: 1; top: 50%; left: 0; width: 30px; height: 1px; background-color: rgba(255, 255, 255, 0.2); }

.header__login { float: right; font-size: 12px; color: rgba(255, 255, 255, 0.4); text-transform: uppercase; transition: all 0.5s ease-in-out; }

.header__login:hover { color: #a70a3f; }

.header__lang { position: relative; position: relative; z-index: 101; float: right; margin-right: 30px; font-size: 12px; text-transform: uppercase; }

.header__lang-link { display: block; position: relative; padding-left: 10px; padding-right: 30px; color: #808080; line-height: 30px; background-color: #ffffff; transition: all 0.5s ease-in-out; }

.header__lang-link:before { content: ''; position: absolute; z-index: 1; top: 50%; right: 9px; width: 0; height: 3px; margin-top: -2px; background-color: transparent; transition: all 0.5s ease-in-out; }

.header__lang_parent > .header__lang-item > .header__lang-link:before, .header__lang_parent > .header__lang-item > .header__lang-link:after { content: ''; position: absolute; display: block; top: 50%; right: 9px; width: 7px; height: 3px; background-color: #4C4C4C; transition: all 0.75s ease-in-out; }

.header__lang_parent > .header__lang-item > .header__lang-link:after { right: 15px; margin-top: -2px; }

.header__lang_parent.is-active > .header__lang-item > .header__lang-link:before, .header__lang_parent.is-active > .header__lang-item > .header__lang-link:after { background-color: #c9c9c9; }

.header__lang_parent.is-active > .header__lang-item > .header__lang-link:before { margin-top: -3px; transform: rotate(-40deg) translateX(-2px) scaleY(1.15); }

.header__lang_parent.is-active > .header__lang-item > .header__lang-link:after { margin-top: -3px; transform: rotate(40deg) translateX(2px) scaleY(1.15); }

.header__lang:not(.header__lang_parent) .header__lang-item:hover > .header__lang-link { color: #a70a3f; }

.header__lang:not(.header__lang_parent) .header__lang-item:hover > .header__lang-link:before { width: 13px; background-color: #a70a3f; }

.header__lang-item.active > .header__lang-link { color: #ffffff; line-height: 38px; }

.header__lang-item.active > .header__lang-link { background-color: transparent; }

.header__lang.is-active > .header__lang-item > .header__lang-link { color: #a70a3f; background-color: #ffffff; }

.header__lang .header__lang { display: none; position: absolute; z-index: 1; top: 100%; }

.header__bot { padding: 14px 0; position: absolute; z-index: 1; top: 100%; left: 0; right: 0; border-bottom: 1px solid rgba(245, 245, 245, 0.1); transform: translateY(40px); transition: all 1s ease-in-out; }

.is-loaded .header__bot { transform: translateY(0); }

.header__logo { float: left; }

.header__nav { float: right; margin-right: -35px; }

/* Nav ----------------------------------- */
.nav { font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; color: #ffffff; text-transform: uppercase; }

.nav:after { content: ""; display: table; clear: both; }

.nav__item { float: left; padding: 20px 35px 20px 36px; }

.nav_header .nav__item { opacity: 0; transform: translateY(-15px); }

.nav__link { display: block; transition: all 0.5s ease-in-out; }

.nav__link:hover { color: #a70a3f; }

.nav_footer { color: #a2a4a8; }

.nav_footer .nav__item { padding-left: 24px; padding-right: 25px; }

/* =================================== Footer @mixin ================================== */
.footer { overflow: hidden; background-color: #f5558b; }

.footer__top { padding-top: 12px; padding-bottom: 12px; background-color: #ffffff; }

.footer__top:after { content: ""; display: table; clear: both; }

.footer__nav { float: left; margin-left: -24px; }

.footer__socials { margin-top: 15px; margin-right: -5px; float: right; }

.footer__bot { padding-top: 13px; padding-bottom: 10px; background-color: #000000; }

.footer__bot:after { content: ""; display: table; clear: both; }

.footer__copy { position: relative; top: 2px; float: left; font-size: 12px; line-height: 15px; color: rgba(255, 255, 255, 0.2); }

.footer__vintage { float: right; font-size: 0; }

.footer__vintage .icon { font-size: 15px; color: rgba(255, 255, 255, 0.4); }

/* Socials ----------------------------------- */
.socials:after { content: ""; display: table; clear: both; }

.socials__item { float: left; }

.socials__item + .socials__item { margin-left: 9px; }

.socials__link { display: block; padding: 5px; font-size: 0; color: #a2a4a8; }

.socials__item:nth-child(1) .icon { font-size: 13px; }

.socials__item:nth-child(2) .icon { font-size: 13px; }

.socials__item:nth-child(3) .icon { margin-top: 1px; font-size: 11px; }

.socials__item:nth-child(4) .icon { margin-top: 1px; font-size: 11px; }

/* =================================== Home Page @mixin ================================== */
/* Screen ----------------------------------- */
.screen { margin-top: -38px; position: relative; color: #ffffff; min-height: 540px; }

.screen .line-vertical { opacity: 0.1; }

.screen__anime { pointer-events: none; position: absolute; z-index: 3; top: 120px; left: 0; right: 0; height: 0; }

.screen__anime:before { content: ''; position: absolute; z-index: 1; left: 50%; margin-left: -611px; top: -1px; width: 20px; height: 3px; background-color: #ffffff; transition: all 1s ease-in-out; transition-delay: 0.5s; }

.screen__anime.is-loaded:before { transform: translateY(84px); }

.screen__anime:after { content: ''; position: absolute; z-index: 1; right: 50%; margin-right: -602px; width: 3px; height: 3px; background-color: #ffffff; transition: all 1s ease-in-out; transition-delay: 0.5s; }

.screen__anime.is-loaded:after { transform: translateY(84px); }

.screen__item { position: relative; height: 100vh; overflow: hidden; }

.screen__slider.is-video .screen__item { pointer-events: none; }

.screen__video { pointer-events: auto; position: absolute; opacity: 0; z-index: 1; top: 50%; left: 50%; transform: scale(1.1) translate(-50%, -50%); min-width: 100%; min-height: 100%; width: auto; height: auto; transition: all 1s ease-in-out; }

.screen__video.is-active { cursor: pointer; opacity: 1; }

.is-loaded .screen__video { transform: scale(1) translate(-50%, -50%); }

.screen__img { position: absolute; z-index: 0; width: 100%; height: 100%; pointer-events: none; background-position: center center; background-repeat: no-repeat; background-size: cover; }

.slick-current .screen__img { transform: scale(1.1); transition: all 1s ease-in-out; }

.is-loaded .slick-current .screen__img { transform: scale(1); }

.screen__container { position: relative; height: 100%; padding-top: 40px; z-index: 2; font-size: 0; text-align: center; }

.screen__container:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.screen__play { position: absolute; z-index: 1; left: 50%; margin-left: -486px; top: 50%; margin-top: 23px; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; color: rgba(255, 255, 255, 0.5); line-height: 1.5; text-transform: uppercase; transition: all 0.5s ease-in-out; }

.screen__play:hover { color: #ffffff; }

.is-loaded .screen__play { transform: translateX(0); }

.screen__play-text { display: inline-block; vertical-align: 2px; opacity: 0; transform: translateY(-20px); }

.screen__play .icon { position: relative; top: -3px; margin-left: 17px; width: 28px; color: #ffffff; opacity: 0; }

.screen__play:before { pointer-events: none; content: ''; position: absolute; z-index: 1; width: 1px; height: 0; bottom: 100%; margin-bottom: 17px; right: 15px; background-color: #ffffff; transition: all 1s ease-in-out; transition-delay: 1s; }

.screen__play:after { pointer-events: none; content: ''; position: absolute; z-index: 1; width: 1px; height: 0; top: 100%; margin-top: 10px; right: 15px; background-color: #ffffff; transition: all 1s ease-in-out; transition-delay: 1s; }

.is-loaded .screen__play:before, .is-loaded .screen__play:after { height: 34px; }

.screen__slider.is-video .screen__play { opacity: 0; }

.screen__inner { margin-top: 125px; display: inline-block; vertical-align: middle; width: 100%; }

.screen__title { max-width: 460px; margin-left: auto; margin-right: auto; text-transform: uppercase; text-align: left; transition: all 0.5s ease-in-out; }

.screen__title p { transform: scale(1.1); transition: all 1s ease-in-out; }

.is-loaded .screen__title p { transform: scale(1); }

.screen__slider.is-video .screen__title { opacity: 0; transform: translateX(50px); }

.screen__btn { position: relative; margin-top: 40px; margin-left: 230px; }

.screen__scroll { position: absolute; bottom: 25px; right: 70px; z-index: 1; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; color: #ffffff; text-transform: uppercase; transition: all 0.5s ease-in-out; }

.screen__scroll-text { display: inline-block; vertical-align: middle; opacity: 0; transform: translateY(-20px); }

.screen__scroll-line { position: absolute; z-index: 1; height: 65px; width: 3px; right: -31px; bottom: -25px; overflow: hidden; transform: translateY(-100%); }

.screen__scroll-line:before { content: ''; position: absolute; z-index: 1; top: 0; left: 0; right: 0; bottom: 0; background-color: #ffffff; }

.screen__scroll-line.is-animated:before { animation: scroll-btn 2s infinite cubic-bezier(0.46, 0.03, 0.52, 0.96); animation-delay: -1s; }

.screen__scroll.is-video { pointer-events: none; opacity: 0; transform: translateY(50px); }

.screen__title { font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 35px; line-height: 1.28571; }

.screen__prev { position: absolute; z-index: 3; bottom: 30px; left: calc(50% - 600px); }

.screen__next { position: absolute; z-index: 3; left: calc(50% - 310px); bottom: 30px; }

.screen__prev, .screen__next { color: rgba(255, 255, 255, 0.5); transition: all 0.5s ease-in-out; }

.screen__prev:hover, .screen__next:hover { color: #ffffff; }

.screen__dots { position: absolute; z-index: 3; bottom: 15px; left: calc(50% - 530px); font-size: 0; transition: all 0.5s ease-in-out; }

.screen__dots:after { content: ""; display: table; clear: both; }

.screen__dots-item { cursor: pointer; display: inline-block; font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 60px; color: #ffffff; }

.screen__dots-item + .screen__dots-item { position: relative; margin-left: 55px; font-size: 24px; color: rgba(255, 255, 255, 0.5); }

.screen__dots-item + .screen__dots-item:before { content: ''; position: absolute; z-index: 1; width: 3px; height: 3px; pointer-events: none; background-color: #ffffff; top: 50%; margin-top: -2px; left: -28px; }

.screen__slider.is-video .screen__dots { pointer-events: none; opacity: 0; transform: translateY(50px); }

@keyframes scroll-btn { from { transform: translateY(-100%); }
  to { transform: translateY(100%); } }

/* Inno ----------------------------------- */
.inno { padding-top: 90px; }

.inno__container:before { content: ''; pointer-events: none; position: absolute; z-index: 1; bottom: 0; right: 38px; width: 3px; height: 3px; background-color: #e8ebf1; }

.inno__container:after { content: ''; pointer-events: none; position: absolute; z-index: 1; bottom: 0; left: 338px; width: 3px; height: 3px; background-color: #e8ebf1; }

.inno__top { position: relative; }

.inno__top:after { content: ""; display: table; clear: both; }

.inno__top:before { content: ''; pointer-events: none; position: absolute; z-index: 1; top: 0; right: 298px; width: 3px; height: 3px; background-color: #e8ebf1; }

.inno__top-col { float: left; }

.inno__top-col:nth-child(1) { position: relative; margin-left: -40px; width: 340px; color: #ffffff; text-transform: uppercase; }

.inno__top-col + .inno__top-col { margin-left: 70px; margin-top: 97px; max-width: 460px; }

