.inno__top-subtitle { font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 18px; line-height: 1.33333; text-transform: uppercase; }

.inno__top-text { margin-top: 36px; font-size: 16px; color: #808080; }

.inno__row { position: relative; padding-bottom: 100px; margin-left: -22px; margin-right: -23px; }

.inno__row:after { content: ""; display: table; clear: both; }

.inno__row:before { content: ""; display: table; clear: both; }

.inno__col { position: relative; margin-top: -131px; float: left; width: 33.33%; padding-left: 22px; padding-right: 23px; }

.inno__item { position: relative; padding-bottom: 85px; position: relative; z-index: 1; }

.inno__item:before { display: block; content: ""; width: 100%; padding-top: 100%; }

.inno__item-inner { position: absolute; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; bottom: 85px; }

.inno__item .motion { overflow: hidden; transform: translate3d(0, 0, 0); }

.inno__item .motion__inner { position: relative; z-index: 1; transform: translate3d(0, 0, 0); }

.inno__item .motion__element { position: absolute; margin-left: 20px; z-index: 2; top: 0; left: 0; right: 0; bottom: 0; width: calc(100% + 20px); background-color: #a70a3f; }

.inno__item-in { transition: all 0.5s ease-in-out; }

.inno__item-video { position: absolute; z-index: 1; top: 0; left: 0; width: 100%; height: 100%; overflow: hidden; transition: all 0.5s ease-in-out; }

.inno__video { position: absolute; z-index: 1; top: 50%; left: 50%; transform: translate(-50%, -50%); height: 100%; }

.inno__desc { position: absolute; margin-top: -84px; top: 100%; left: 0; opacity: 0; z-index: 2; background-color: #ffffff; padding: 0 0 10px 0; width: calc(100% - 70px); height: 240px; }

.inno__desc.is-transition { transition: all 0.75s ease-in-out; }

